import React from 'react';
import {datalist} from "../action/Productaction";
import {connect} from "react-redux";
import { Button, TabBar } from 'antd-mobile';
class PicDemo extends React.Component {
    constructor(props) {
        super(props);
    }
    componentDidMount(){
        this.props.dispatch(datalist());
    }
    render() {
        const pic = this.props.datalist.map((item)=>{
            const Id = localStorage.getItem('key');
            if(item.id==Id){
                return <img src={item.img} style={{width:"100%"}} alt=""/>;
            }
        })
        return (
            <div>
                {/* <div>
                    <i className='iconfont icon-fanhui' style={{display:"block"}} onClick={()=>this.props.history.push('/home')}></i>
                    <img src={require('../image/caidao2.png')}
                    alt=""
                    style={{ width:300,height:300,verticalAlign: 'top' }}/> 
                </div> */}
                <div>
                    <i className="iconfont icon-fanhui" onClick={()=>this.props.history.goBack()} style={{top:10}}></i>
                    {pic}
                </div>
                <div style={{padding:10,height:60,marginTop:100}}>
                    <button onClick={()=>{this.props.history.push('/')}}>
                        <i className='iconfont icon-dianpu' style={{display:"block"}} onClick={()=>this.props.history.push('/home')}></i>
                        <p>店铺</p>
                    </button>        
                    <button onClick={()=>{this.props.history.push('/')}}>
                        <i className='iconfont icon-kefu'  onClick={()=>this.props.history.push('/home')}></i>
                        <p>客服</p>
                    </button>   
                    <button onClick={()=>{this.props.history.push('/')}}>
                        <i className='iconfont icon-shoucang' style={{display:"block"}} onClick={()=>this.props.history.push('/home')}></i>
                        <p>收藏</p>
                    </button>  
                    <button onClick={()=>{this.props.history.push('/')}} style={{width:100,backgroundColor:'orange'}}>  
                    <p>加入购物车</p>
                    </button>  

                    <button onClick={()=>{this.props.history.push('/')}} style={{width:100,backgroundColor:'red'}}>
                        <p>立即购买</p>
                    </button>   
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state)=>({
    datalist:state.Homereducer.datalist
})
export default connect(mapStateToProps)(PicDemo);